.hero {
    padding: 118px 0;
    text-align: center;

    background-color: #c4c4c4;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-image: $linearGradient, image-set(url("../img/hero/hero-480.jpg") 1x,
            url("../img/hero/hero-480@2x.jpg") 2x);
    // background-image: $linearGradient, url(../img/hero/hero-480.jpg);

    // @media screen and (min-device-pixel-ratio: 2),
    // screen and (min-resolution: 192dpi),
    // screen and (min-resolution: 2dppx) {
    //     background-image: $linearGradient, url("../img/hero/hero-480@2x.jpg");
    // }


    @media screen and (min-width: 768px) {
        background-image: $linearGradient,
            image-set(url("../img/hero/hero-768.jpg") 1x,
                url("../img/hero/hero-768@2x.jpg") 2x);
        // background-image: $linearGradient, url(../img/hero/hero-768.jpg);

        // @media screen and (min-device-pixel-ratio: 2),
        // screen and (min-resolution: 192dpi),
        // screen and (min-resolution: 2dppx) {
        //     background-image: $linearGradient, url("../img/hero/hero-768@2x.jpg");
        // }
        // }

        @media screen and (min-width: 1200px) {
            background-size: 1600px;
            padding: 200px 0;
            background-image: linear-gradient(0deg, rgba(47, 48, 58, 0.4),
                    rgba(47, 48, 58, 0.4)),
                image-set(url("../img/hero/hero-1600.jpg") 1x,
                    url("../img/hero/hero-1600@2x.jpg") 2x);
            //     background-image: $linearGradient, url(../img/hero/hero-1600.jpg);

            //     @media screen and (min-device-pixel-ratio: 2),
            //     screen and (min-resolution: 192dpi),
            //     screen and (min-resolution: 2dppx) {
            //         background-image: $linearGradient, url("../img/hero/hero-1600@2x.jpg");
            //     }
        }

        &__title {
            padding: 0 80px;
            margin-bottom: $prime-gap;
            font-weight: 900;

            @include font(1.56px, 26px, calc(42 / 26));
            text-transform: uppercase;
            color: getColor('light');

            @media screen and (min-width: 768px) {
                padding: 0 220px;
            }

            @media screen and (min-width: 1200px) {
                @include font(2.64px, 44px, calc(60 / 44));
                padding: 0 340px;
            }
        }
    }
}